<template>
    <div class="login">
        <top-title :type="true"></top-title>
        <f-alert></f-alert>
        <div class="login-bg" :style="{backgroundImage:`url('/static/user/bg2.jpg')`}">
            <div class="login-box">
                <div class="box-title">
                    <p>欢迎登录福分通平台</p>
                    <p style="font-size: 16px;margin-top: 10px">员工入口</p>
                </div>
                <div class="box-form">
                    <!--输入用户名-->
                    <div class="user form-item">
                        <label for="user-id">
                            <input id="user-id" type="text" name="用户名" placeholder="请输入手机号码/用户名"
                                   v-model="phone">
                        </label>
                    </div>
                    <!--选择子站-->
                    <div class="form-item check-business" v-if="business_list.length>1">
                        <div class="business-che" @click="show_business_list = !show_business_list">
                            {{select_business.business_name?select_business.business_name:'请选择需要登录的子站'}}
                            <input id="business-code"
                                   type="hidden"
                                   name="business-code"
                                   placeholder="请选择需要登录的子站"

                                   v-model="select_business.business_name"
                            >
                        </div>
                        <ul v-if="show_business_list" class="business-list">
                            <li v-for="(i,k) in business_list" @click="selectBusiness(k)">{{i.business_name}}</li>
                        </ul>
                    </div>
                    <div class="message form-item"  v-if="type === 2">
                        <label for="msg">
                            <input id="msg" type="text" name="验证码" placeholder="请输入验证码" v-model="password"
                                   @keyup.enter="login()">
                        </label>
                        <div  class="decode" >
                            <Msg :phone="phone"></Msg>
                        </div>
                        <!--<div v-else class="decode" @click="refused()">-->
                            <!--{{timeOut}}-->
                        <!--</div>-->
                    </div>
                    <div class="message form-item" v-if="type === 1">
                        <label for="msg2">
                            <input id="msg2" type="password" name="密码" placeholder="请输入密码" v-model="password"
                                   @keyup.enter="login()">
                        </label>
                    </div>
                    <div class="sign-in form-item" @click="login()">
                        <input type="button" value="登录" >
                    </div>
                    <div class="save-login">
                        <a class="fl" v-if="type===2" @click="changeType">使用密码登录</a>
                        <a class="fl" v-if="type===1" @click="changeType">使用验证码登录</a>
                        <a class="fr" href="/user/card-login.html">
                            使用兑换卡兑换
                        </a>
                    </div>
                </div>
            </div>
            <div class="set-phone-mask" v-if="needPhone">
                <div class="phone-box login-box">
                    <div class="box-title">
                        <p>绑定手机号码</p>
                        <!--<p style="font-size: 16px;margin-top: 10px">请先绑定手机号码</p>-->
                    </div>
                    <div class="box-form">
                        <!--输入手机-->
                        <div class="user form-item">
                            <label for="user-phone">
                                <input id="user-phone" type="text" name="用户名" placeholder="请输入手机号码/用户名"
                                       v-model="checkphone">
                            </label>
                        </div>
                        <div class="message form-item">
                            <label for="sdmsg">
                                <input id="sdmsg" type="text" name="验证码" placeholder="请输入验证码" v-model="password"
                                       @keyup.enter="bindPhone()">
                            </label>
                            <div class="decode">
                                <Msg :phone="checkphone"></Msg>
                            </div>
                        </div>
                        <div class="sign-in form-item" @click="bindPhone()">
                            <input type="button" value="绑定手机" >
                        </div>
                    </div>
                </div>
            </div>
            <div class="login-info">
                <p style="margin-bottom: 10px">关于我们 | 福分通商城 | 增值通 | 维修通 | officemate办公伙伴</p>
                <p>版权所有 © CopyRight 2017 欧菲斯办公伙伴控股有限公司 渝ICP备18008342号-2</p>
            </div>
        </div>
    </div>
</template>

<script>
    import TopTitle from '../../../components/TopTile'
    import FAlert from '../../../components/FAlert'
    import Msg from '../../../components/VerifyCode'
    import axios from 'axios'
    import qs from 'qs';
    import promise from 'es6-promise'
    promise.polyfill();
    export default {
        metaInfo: {
            title: '福分通-用户登录', // set a title
            meta: [{                 // set meta
                name: '福分通',
                content: '专注企业员工福利'
            }]
        },
        data() {
            return {
                phone: '',
                checkphone:'',
                password: '',
                access: false,
                isAccess:false,
                timeOut: '获取验证码',
                disabled:false,
                type:2,
                show_business_list:false,
                business_list:[],
                select_business:{
                    business_name:''
                },
                needPhone:false,
                token:''
            }
        },
        components: {
            TopTitle,
            FAlert,
            Msg
        },
        mounted(){
            var _self = this;
            this.$watch('phone', _self.debounce(function(){
                _self.getBusinessList()
            }, 800));
        },
        watch:{
            phone:{
                handler(e){
                    let testPhone = /^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/;
                    this.access = testPhone.test(e);
                },
                deep:true
            }
            // access:{
            //     handler(e){
            //         if (e){
            //             setTimeout(()=>{
            //                 this.getBusinessList()
            //             },800)
            //         }else{
            //             this.business_list = []
            //         }
            //     }
            // }
        },
        methods: {
            debounce(func, delay) {
                var timer;
                return function (...args) {
                    if (timer) {
                        clearTimeout(timer)
                    }
                    timer = setTimeout(function() {
                        func()
                    }, delay)
                }
            },
            changeType(){
                this.type ===1?this.type=2:this.type=1;
                if (this.type ===1){
                    this.access = true
                } else {
                    this.access = false
                }
                this.password = ''
            },
            refused(){
              if (this.disabled){
                  this.bus.$emit('notice','短信已发送')
              } else{
                  this.bus.$emit('notice','账号不存在')
              }
            },
            sendMsg(phone) {
                if (this.access === true&&!this.disabled) {
                    this.yum('api/verify/sendSms', {phone: phone}).then(
                        (res) => {
                            if (res.data.status === 200) {
                                this.MsgTimeOut()
                                this.bus.$emit('notice',res.data.msg)
                            }
                            else {
                                return false
                            }
                        }
                    )
                } else {
                    this.bus.$emit('notice','请验证手机号码')
                }
            },
            MsgTimeOut() {
                let time = 60;
                let _self = this;
                _self.disabled = true;
                let timer = setInterval(function () {
                    time -= 1;
                    _self.timeOut = `请等待${time}秒`;
                    if (time === 0) {
                        clearInterval(timer);
                        _self.disabled = false;
                        _self.timeOut = '获取验证码'
                    }
                }, 1000)
            },
            login(){
                let data ={
                    loginName:this.phone,
                    type:this.type,
                    password:this.password
                };
                for (let i in data){
                    if (!data[i]){
                        this.bus.$emit('notice','请检查账号密码是否填写！');
                        return false
                    }
                }
                if (!this.select_business.business_code){
                    this.bus.$emit('notice','没有选择需要登录的子站！');
                    return false
                }
                data = qs.stringify(data);
                let url = `http://mapi.fufentong.com/${this.select_business.business_code}/api/member/login`;
                axios.post(url,data).then(res=>{
                    if (res.data.status === 200){
                        this.store.addStorage('FFT',res.data.data);
                        this.token = res.data.data.token;
                        this.isAccess = false;
                        this.password = '';
                        this.getCurrentBusiness();
                    }else{
                        this.bus.$emit('notice',res.data.msg);
                    }
                })
            },
            async bindPhone(){
                let data = {
                    verify_code:this.password,
                    mobile:this.checkphone,
                    token:this.token
                };
                await this.yum('api/member/bindPhone',data).then(res=>{
                    if (res.data.status === 200){
                        let user = this.store.getStorage('FFT');
                        user = Object.assign(user,{mobile:this.checkphone});
                        this.store.addStorage('FFT',user);
                        location.assign('../index.html')
                    }else{
                        this.bus.$emit('notice',res.data.msg);
                    }
                })
            },
            getBusinessList(){
                this.yum('api/member/checkMemberOfPhone',{mobile:this.phone}).then(res=>{
                    if (res.data.status===200){
                        this.business_list = res.data.data;
                        //短信防刷
                        this.isAccess = true;
                        if (this.business_list.length===1){
                            this.select_business = this.business_list[0]
                        }
                    }else{
                        this.bus.$emit('notice',res.data.msg);
                        this.isAccess = false;
                        this.business_list = [];
                    }
                })
            },
            getCurrentBusiness(){
                axios.post(`http://mapi.fufentong.com/${this.select_business.business_code}/api/business/getCurrentBusiness`)
                    .then(res=>{
                    if (res.data.status === 200){
                        this.store.addStorage('business_data',res.data.data);
                        if (this.store.getStorage('FFT').mobile){
                            console.log('mobile')
                            location.assign('../index.html')
                        } else{
                            this.needPhone = true
                        }
                        // location.assign('../index.html')
                    }
                })
            },
            selectBusiness(i){
                this.show_business_list = false;
                this.select_business = this.business_list[i]
            }
        }
    }
</script>

<style lang="stylus" scoped>

    .login-bg
        width 100%
        min-width 1200px
        min-height 900px
        height 100%
        position absolute
        top 0
        left 0
        z-index -1
        background-position 50% 0
        background-repeat no-repeat
        background-size 1920px 974px
        background-color black
        text-align center
        input,div.business-che
            background-color white
            display inline-block
            width 100%
            height 100%
            line-height 50px
            outline none
            border solid 1px #999999
            border-radius 4px
            padding-left 10px
            font-size 14px
            opacity 0.8
        input[name= '验证码']
            width 240px
            position absolute
            bottom 0
            opacity 0.8
        input[type= button]
            padding-left 0
            background-color rgba(255,0,119,0.8)
            border none
            color white
            font-size 18px
            font-weight bold
            cursor pointer
        .set-phone-mask
            position absolute
            width 100%
            height 100%
            top 0
            left 0
            z-index 99
            background-color rgba(0,0,0,0.5)
            .phone-box
                display block
                margin 210px auto 200px
                width 480px
                background-color rgba(254,254,254,1)
        .login-box
            display block
            margin 210px auto 200px
            width 480px
            background-color rgba(254,254,254,0.6)
            border-radius 6px
            box-shadow  0 0 8px 8px rgba(0,0,0,0.3)
            .box-title
                padding 20px
                font-size 24px
                font-weight bold
                color black
                border-bottom 1px solid #ccc
            .box-form
                display block
                padding 40px 50px
                .form-item
                    position relative
                    width 100%
                    height 50px
                    margin-bottom 20px
                    text-align left
                    transition 0.3s
                    img.icon
                        width 20px
                        top 15px
                        left 10px
                        display inline-block
                        position absolute
                    .business-list
                        position absolute
                        box-sizing border-box
                        width 100%
                        border 1px solid #999
                        border-radius 4px
                        background-color white
                        z-index 5
                        max-height 200px
                        overflow-y scroll
                        li
                            padding 10px
                            transition 0.3s
                            &:hover
                                cursor pointer
                                color white
                                background-color #ff0077
            .message
                .decode
                    position absolute
                    bottom 0
                    right 0
                    cursor pointer
                    font-size 16px
                    user-select none
                    text-align center
                    display inline-block
                    height 50px
                    width 130px
                    line-height 48px
                    margin-left 10px
                    border solid 1px #999999
                    border-radius 4px
                    opacity: 0.6;
                    background-color: #ff0077;
                    color: white;
            .save-login
                text-align left
                user-select none
        .login-info
            border 1px solid rgba(0, 0, 0, 0)
            margin-top 50px
            position absolute
            width 100%
            bottom 50px
            p
                color white
                a
                    color white
</style>
